import React, { Fragment } from "react";

import FormRow from "../StyledComponents/FormRow";
import FieldSet from "../StyledComponents/FieldSet";
import InputGroup from "../InputGroup";
import SelectGroup from "../SelectGroup";

import getStateOptions from "../../../helpers/renderStateOptions";
import { countries } from "../../../config/dropdowns.json";

function AddressBlock({
	fields,
	errors,
	handleInputChange,
	handleBlur,
	getAddress,
	getPhone,
	allowInternational,
	type,
	hideAddressTwo,
}) {
	return (
		<FieldSet className="address-block">
			<legend>{`${type} Address Block`}</legend>
			{getAddress && (
				<Fragment>
					<FormRow className="address1-row">
						<InputGroup
							type="text"
							id="Address1"
							specialStyle="form-group--Address1"
							label="Address"
							placeholder="Address*"
							maxLength="31"
							required={true}
							value={fields.Address1}
							handleInputChange={handleInputChange}
							handleBlur={handleBlur}
							error={errors.Address1}
						/>
					</FormRow>
					{!hideAddressTwo && (
						<FormRow className="address2-row">
							<InputGroup
								type="text"
								id="Address2"
								specialStyle="form-group--Address2"
								label="Address2"
								placeholder="Address Line 2"
								maxLength="31"
								required={false}
								value={fields.Address2}
								handleInputChange={handleInputChange}
								handleBlur={handleBlur}
								error={errors.Address2}
							/>
						</FormRow>
					)}
					<FormRow className="city-state-row">
						<InputGroup
							type="text"
							id="City"
							specialStyle="form-group--City"
							label="City"
							placeholder="City*"
							maxLength="28"
							required={true}
							value={fields.City}
							handleInputChange={handleInputChange}
							handleBlur={handleBlur}
							error={errors.City}
						/>
						<SelectGroup
							id="State"
							label="State"
							specialStyle="form-group--State"
							required={true}
							value={fields.Country == "United States" ? fields.State : "00"}
							error={errors.State}
							handleInputChange={handleInputChange}
							handleBlur={handleBlur}
							required={fields.Country == "United States"}
							disabled={fields.Country != "United States"}
							options={[
								<option key="state-base-0" value="" disabled="disabled">
									State* &#9660;
								</option>,
								getStateOptions(allowInternational),
							]}
						/>
					</FormRow>

					<FormRow className="zip-country-row">
						<InputGroup
							type="text"
							id="Zip"
							specialStyle="form-group--Zip"
							label="Zip"
							placeholder="Zip*"
							maxLength={fields.Country != "United States" ? 25 : 5}
							required={fields.Country == "United States"}
							disabled={fields.Country != "United States"}
							value={fields.Country == "United States" ? fields.Zip : "NA"}
							handleInputChange={handleInputChange}
							handleBlur={handleBlur}
							error={errors.Zip}
							allowInternational={allowInternational}
							validation={fields.Country != "United States" ? "*" : "d*"}
						/>

						{allowInternational && (
							<SelectGroup
								id="Country"
								label="Country"
								specialStyle="form-group--Country"
								required={true}
								value={fields.Country}
								error={errors.Country}
								handleInputChange={handleInputChange}
								handleBlur={handleBlur}
								options={[
									<option key="country-base-0" value="" disabled="disabled">
										Country* &#9660;
									</option>,
									countries.map((country, i) => (
										<option key={`country-${i}`} value={country}>
											{country}
										</option>
									)),
								]}
							/>
						)}
					</FormRow>
				</Fragment>
			)}
			<FormRow className="email-phone-row">
				<InputGroup
					type="text"
					id="Emailaddress"
					specialStyle="form-group--Email"
					label="Email Address"
					placeholder="Email Address*"
					maxLength="128"
					required={true}
					value={fields.Emailaddress}
					handleInputChange={handleInputChange}
					handleBlur={handleBlur}
					error={errors.Emailaddress}
				/>
				{getPhone && (
					<InputGroup
						type="text"
						id="phone"
						specialStyle="form-group--Phone"
						label="Phone Number"
						placeholder="Phone"
						maxLength="24"
						required={false}
						value={fields.phone}
						disabled={fields.Country != "United States"}
						handleInputChange={handleInputChange}
						handleBlur={handleBlur}
						error={errors.phone}
						validation="\d*"
					/>
				)}
			</FormRow>
		</FieldSet>
	);
}

export default AddressBlock;
